<template>
  
  <div v-show="step===0">
    <Post v-for="(post, i) in propsData" :key="i"
    :post="post" />
  </div>
  

  <!-- ÌïÑÌÑ∞ÏÑ†ÌÉùÌéòÏù¥ÏßÄ -->
  <div v-show="step===1">
    <div class="upload-image" :class="imageFilter" :style="{backgroundImage: `url(${uploadUrl})`}"></div>
    <div class="filters">
      <FilterBox v-for="(name,i) in filterNames" :key="i"
          :filterName="name" :uploadUrl="uploadUrl">
        {{name}}
      </FilterBox>
    </div>
  </div>
  

  <!-- Í∏ÄÏûëÏÑ±ÌéòÏù¥ÏßÄ -->
  <div v-show="step===2">
    <div class="upload-image" :class="imageFilter" :style="{backgroundImage: `url(${uploadUrl})`}"></div>
    <div class="write">
      <textarea class="write-box" v-model="content" @input="$emit('write', $event.target.value)"></textarea>
    </div>
  </div>
  
  
</template>

<script>
import Post from '@/components/Post.vue';
import FilterBox from '@/components/FilterBox.vue';

export default {
  props: {
    propsData: Array,
    step: Number,
    uploadUrl: String,
    imageFilter: String,
  },
  emits: ['write'],
  components: {
    Post, FilterBox,
  },
  data() {
    return {
      content: '',
      filterNames: [ "aden", "_1977", "brannan", "brooklyn", "clarendon", "earlybird", "gingham", "hudson", 
                "inkwell", "kelvin", "lark", "lofi", "maven", "mayfair", "moon", "nashville", "perpetua", 
                "reyes", "rise", "slumber", "stinson", "toaster", "valencia", "walden", "willow", "xpro2"],
    }
  },
  watch: {
    step() {
      this.content = '';
    }
  }
}
</script>

<style>
.upload-image{
  width: 100%;
  height: 450px;
  background: cornflowerblue;
  background-size : cover;
}
.filters{
  overflow-x:scroll;
  white-space: nowrap;
}
.filter-1 {
  width: 100px;
  height: 100px;
  background-color: cornflowerblue;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color : white;
  background-size: cover;
}
.filters::-webkit-scrollbar {
  height: 5px;
}
.filters::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
.filters::-webkit-scrollbar-thumb {
  background: #888; 
  border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
  background: #555; 
}
.write-box {
  border: none;
  width: 90%;
  height: 100px;
  padding: 15px;
  margin: auto;
  display: block;
  outline: none;
}
</style>