<template>
  <div :class="filterName+' filter-item'" 
    :style="{backgroundImage: `url(${uploadUrl})`}"
    @click="fire"
  >
    <slot></slot>
  </div>
</template>

<script>
export default {
  props: {
    uploadUrl: String,
    filterName: String,
  },
  methods: {
    fire() {
      this.emitter.emit('changeFilter', this.filterName)
    },
  },
}
</script>

<style scoped>
.filter-item {
  width: 100px;
  height: 100px;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color : white;
  background-size: cover;
  background-position : center;
}
</style>