<template>
  <div class="header">
    <ul class="header-button-left" v-if="step>0">
      <li @click="step=0">Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step==1" @click="step++">Next</li>
      <li v-if="step==2" @click="publish">Î∞úÌñâ</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

  <div>
    {{more}}
    <button @click="$store.dispatch('getData')">ÎçîÎ≥¥Í∏∞</button>
  </div>
  
  <Container :step="step" :uploadUrl="uploadUrl" :imageFilter="imageFilter" :propsData="posts" @write="writeContent" />

  <button v-if="step==0" @click="more">ÎçîÎ≥¥Í∏∞</button>

  <div class="footer" v-if="step==0">
    <ul class="footer-button-plus">
      <input @change="upload" accept="image/*" type="file" id="file" class="inputfile" />
      <label for="file" class="input-plus">+</label>
    </ul>
 </div>

</template>

<script>
import Container from '@/components/Container.vue';
import data from '@/data.js';
import axios from 'axios';
import {mapState, mapMutations} from 'vuex';

export default {
  name: 'App',
  components: {
    Container,
  },
  data() {
    return {
      posts: [...data],
      moreNumber: 0,
      step: 0,
      uploadUrl: '',
      content: '',
      imageFilter: 'aden',
    }
  },
  computed: {
    name() {
      return this.$store.state.name;
    },
    ...mapState(['name', 'age', 'more']),
    ...mapState({ÎÇ¥Ïù¥Î¶Ñ : 'name', }),
  },
  methods: {
    ...mapMutations(['setMore', 'increaseAge']),

    async more() {
      const url = `https://codingapple1.github.io/vue/more${this.moreNumber}.json`;
      try {
        const res = await axios.get(url);
        this.posts.push(res.data);

        this.moreNumber ++;
      } catch(err) {
        console.log(err);
      }
    },
    upload(e) {
      let files = e.target.files;
      this.uploadUrl = URL.createObjectURL(files[0]);
      this.step = 1;
    },
    publish() {
      const obj = {
        name: "Kim Hyun",
        userImage: this.uploadUrl,
        postImage: this.uploadUrl,
        content: this.content,
        likes: 0,
        date: "May 15",
        liked: false,
        filter: this.imageFilter,
      };

      this.posts.unshift(obj);
      this.step = 0;
      this.content = '';
    },
    writeContent(text) {
      this.content = text;
    },
  },
  mounted() {
    this.emitter.on('changeFilter', (name) => {
      this.imageFilter = name;
    });
  }
}
</script>

